import dataStorage from './../tools/storage.js';
import markup from './../markup/tools.js';
import formTools from './../tools/form-tools.js';

let appUrl;
let messages;

let form;
let price;
let amount;
let modal;

const initVar = (remModal) => {
  modal = remModal;
  form = modal.querySelector('*[data-formName]');
  price = form.querySelector('*[data-valid="price"]');
  amount = form.querySelector('*[data-valid="amount"]');

  appUrl = window.appSettings[form.dataset.formname].UrlApi;
  messages = window.appSettings[form.dataset.formname].messages;

};

const callbackXhrSuccess = (response) => {
  switch (response.status) {
  case 270:
    $(modal).modal('hide');
    markup.informationtModal = {
      'title': 'MESSAGE: ',
      'message': response.message,
      'isMess': true
    };
    break;
  case 400:
    markup.informationtModal = {
      'title': 'ERROR: ',
      'message': messages.mes400
    };
    break;
  case 271:
    markup.informationtModal = {
      'title': 'ERROR: ',
      'message': response.messages
    };
    break;
  }
};

const callbackXhrError = (xhr) => {

  $(modal).modal('hide');
  formTools.reset();

  markup.informationtModal = {
    'title': 'ОШИБКА СВЯЗИ',
    'message': `Ошибка ${xhr.status}: ${xhr.statusText}`
  };
};

const submitForm = () => {
  const stor = dataStorage.data;
  let value = amount.value * Number(dataStorage.expressOperationType);

  let postData = `token=${stor.token}&value=${value}&price=${price.value}`;
  let urlApp = appUrl.replace('{{dir}}', stor.directory);
  urlApp = urlApp.replace('{{oper}}', stor.operatorId);
  urlApp = urlApp.replace('{{busId}}', stor.currentBusiness);
  urlApp = urlApp.replace('{{goodId}}', dataStorage.currentGoodId);
  urlApp = urlApp.replace('{{stockId}}', dataStorage.currentStockId);

  formTools.submit({
    url: urlApp,
    metod: 'POST',
    data: postData,
    callbackSuccess: callbackXhrSuccess,
    callbackError: callbackXhrError
  });
};

export default {
  start(remModal) {
    initVar(remModal);
    formTools.work(modal, submitForm);
  },
  stop() {
    formTools.reset();
  }
};



